import {Navbar, Nav, NavDropdown} from 'react-bootstrap'
import {Link, useHistory} from 'react-router-dom'


function Header() 
{
	let user = JSON.parse(localStorage.getItem('user-info'))

	const history = useHistory()

	function logOut() {
		localStorage.clear();
		history.push("/reactjs/login")
	}

	return(
		<div>
			<Navbar bg="dark" variant="dark">
			    <Navbar.Brand href="/reactjs/login">SmartContract</Navbar.Brand>

			    <Nav className="mr-auto navbar-wrapper">
			    {
			    	localStorage.getItem('user-info') ?
			    	<>
				    	<Link to="/reactjs/dashboard">Dashboard</Link>
				    	<Link to="/reactjs/profile">Profile</Link>
			    	</>
			    	:
			    	<>
				    	<Link to="/reactjs/login">Login</Link>
				    	<Link to="/reactjs/register">Register</Link>
			    	</>
			    }
			    </Nav>

			    {
				    localStorage.getItem('user-info') ?
				    <Nav>
				    	<NavDropdown title={user && user.name}>
				    		<NavDropdown.Item onClick={logOut}>Logout</NavDropdown.Item>
				    	</NavDropdown>
				    </Nav>
				    : null
				}

			</Navbar>
		</div>
	)
}

export default Header;