import './App.css';

import {BrowserRouter, Route} from 'react-router-dom'

import Login from './Login'
import Register from './Register'
import Dashboard from './Dashboard'
import Profile from './Profile'

import Protected from './Protected'

function App() 
{
  return (
    <div className="App">
      <BrowserRouter>
        {/*<h1>SmartContract Project</h1>*/}
        <Route path="/reactjs/login">
          <Login />
        </Route>
        <Route path="/reactjs/register">
          <Register />
        </Route>
        <Route path="/reactjs/dashboard">
          <Protected Cmp={Dashboard} />
          {/*<Dashboard />*/}
        </Route>
        <Route path="/reactjs/profile">
          <Protected Cmp={Profile} />
          {/*<Profile />*/}
        </Route>
      </BrowserRouter>
    </div>
  );
}

export default App;
